import React from "react";
import { BrowserRouter, Route } from "react-router-dom";
import Home from "./containers/Home";
import Game01 from "./containers/Game01";
import Cricket from "./containers/Cricket";
import CountUp from "./containers/CountUp";

class App extends React.Component {
  render() {
    return (
      <BrowserRouter>
        <div>
          <Route exact path='/dartslife' component={Home} />
          <Route path='/dartslife/game01' component={Game01} />
          <Route path='/dartslife/cricket' component={Cricket} />
          <Route path='/dartslife/countup' component={CountUp} />
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
