import React from "react";

class RoundCountUp extends React.Component {
  render() {
    const { currentRound } = this.props;
    const finishRound = currentRound < 8;
    return (
      <div>
        <p
          style={{
            fontSize: "24px",
            fontWeight: "bold",
            textShadow: "5px 5px 5px grey",
          }}
        >
          {finishRound ? `Round ${currentRound + 1}` : "Finish!!"}
        </p>
      </div>
    );
  }
}
export default RoundCountUp;
