import React from "react";

class Form01 extends React.Component {
  constructor(props) {
    super(props);
    this.state = { input: "" };
  }
  render() {
    const { currentScoreA, currentScoreB, currentId } = this.props;
    const finish =
      currentScoreA === 0 || currentScoreB === 0 || currentId === 15;
    return (
      <div>
        {finish ? (
          <form>
            <button
              onClick={this.handleClick}
              style={{
                color: "white",
                backgroundColor: "rgba(238,238,238,0.3)",
                padding: "5px",
                borderRadius: "8px",
                margin: "20px",
              }}
            >
              Continue
            </button>
          </form>
        ) : (
          <form onSubmit={this.handleSubmit}>
            <input
              type='text'
              value={this.state.input}
              onChange={this.handleChange}
              style={{
                color: "#eeeeee",
                width: "100px",
                height: "20px",
                backgroundColor: "rgba(238,238,238,0.3)",
                margin: "30px 30px 20px",
                borderRadius: "8px",
              }}
            />
            <button
              style={{
                color: "white",
                backgroundColor: "rgba(238,238,238,0.3)",
                padding: "5px",
                borderRadius: "8px",
              }}
            >
              Next
            </button>
          </form>
        )}
      </div>
    );
  }
  handleChange = (e) => {
    const re = /^[0-9\b]+$/;
    if (e.target.value === "" || re.test(e.target.value)) {
      const newInput = e.currentTarget.value;
      this.setState({ input: newInput });
    }
  };

  handleSubmit = (e) => {
    e.preventDefault();
    if (
      this.state.input > 180 ||
      this.state.input < 0 ||
      this.state.input === ""
    ) {
      alert("0~180の数字を入力してね");
      return;
    }
    const { onSubmit } = this.props;
    onSubmit(this.state.input);
    this.setState({ input: "" });
  };

  handleClick = (e) => {
    e.preventDefault();
    const { onClick } = this.props;
    return onClick();
  };
}
export default Form01;
