import React from "react";

class TableCountUp extends React.Component {
  render() {
    const { results } = this.props;
    const table = results.map((result) => (
      <tr key={result.round} align='center'>
        <td>{`R${result.round + 1}`}</td>
        <td>{result.score}</td>
      </tr>
    ));
    return (
      <table
        width='200px'
        border='2px'
        style={{
          color: "#eeeeeee",
          borderColor: "white",
          boxShadow: "5px 5px 5px grey",
        }}
      >
        <thead>
          <tr>
            <th>Round</th>
            <th>Score</th>
          </tr>
        </thead>
        <tbody style={{ textShadow: "5px 5px 5px grey" }}>{table}</tbody>
      </table>
    );
  }
}
export default TableCountUp;
