import React from "react";

class Table extends React.Component {
  render() {
    const { results } = this.props;
    const currentRoundScore = results.map((result) => {
      return (
        <tr key={result.id} align='center'>
          <td>{`R${result.id + 1}`}</td>
          <td>{result.scoreA}</td>
          <td>{result.scoreB}</td>
        </tr>
      );
    });
    return (
      <table
        border='1px'
        width='300px'
        style={{
          color: "#eeeeeee",
          borderColor: "white",
          boxShadow: "5px 5px 5px grey",
        }}
      >
        <thead>
          <tr>
            <th>Round</th>
            <th>Player A</th>
            <th>Player B</th>
          </tr>
        </thead>
        <tbody>{currentRoundScore}</tbody>
      </table>
    );
  }
}
export default Table;
