import React from "react";

class TotalScoreCountUp extends React.Component {
  render() {
    const scoreAverage = Math.floor(
      this.props.totalScore / this.props.currentRound
    );
    let scorePrediction;
    if (this.props.currentRound === 0) {
      scorePrediction = 0;
    } else {
      scorePrediction = scoreAverage * 8;
    }
    const { currentRound } = this.props;
    const finishRound = currentRound < 8;

    return (
      <div
        style={{
          textAlign: "center",
          display: "inline-block",
          margin: "110px 30px 80px auto",
          width: "60%",
        }}
      >
        <h3 style={{ fontSize: "40px", textShadow: "5px 5px 5px grey" }}>
          {finishRound ? `Score Prediction ${scorePrediction}` : "Your Score"}
        </h3>
        <h2
          style={{
            fontSize: "60px",
            marginTop: "70px",
            textShadow: "5px 5px 5px grey",
          }}
        >
          {this.props.totalScore}
        </h2>
      </div>
    );
  }
}
export default TotalScoreCountUp;
