import React from "react";

class Score extends React.Component {
  render() {
    const {
      currentScoreA,
      currentScoreB,
      currentId,
      currentPlayer,
    } = this.props;
    const finish =
      currentScoreA === 0 || currentScoreB === 0 || currentId === 15;
    return (
      <div
        style={{
          textAlign: "center",
          display: "inline-block",
          margin: "80px 30px 80px auto",
          width: "60%",
        }}
      >
        {finish ? (
          <div
            style={{
              fontSize: "60px",
              marginTop: "100px",
              textShadow: "5px 5px 5px grey",
            }}
          >
            <h3
              style={{ fontSize: "40px", textShadow: "5px 5px 5px grey" }}
            >{`Winner ${currentPlayer ? "A" : "B"}`}</h3>
            <h2
              style={{
                fontSize: "60px",
                marginTop: "70px",
                textShadow: "5px 5px 5px grey",
              }}
            >
              {currentPlayer ? currentScoreA : currentScoreB}
            </h2>
          </div>
        ) : (
          <div
            style={{
              fontSize: "60px",
              marginTop: "100px",
              textShadow: "5px 5px 5px grey",
            }}
          >
            <div style={{ display: "inline-block", paddingRight: "60px" }}>
              <h3 style={{ fontSize: "40px", textShadow: "5px 5px 5px grey" }}>
                Player A
              </h3>
              <h2
                style={{
                  fontSize: "60px",
                  marginTop: "70px",
                  textShadow: "5px 5px 5px grey",
                }}
              >
                {currentScoreA}
              </h2>
            </div>
            <div style={{ display: "inline-block" }}>
              <h3 style={{ fontSize: "40px", textShadow: "5px 5px 5px grey" }}>
                Player B
              </h3>
              <h2
                style={{
                  fontSize: "60px",
                  marginTop: "70px",
                  textShadow: "5px 5px 5px grey",
                }}
              >
                {currentScoreB}
              </h2>
            </div>
          </div>
        )}
      </div>
    );
  }
}
export default Score;
