import React from "react";

class FormCountUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input: "",
    };
  }

  render() {
    const { currentRound } = this.props;
    const finish = currentRound < 8;
    return (
      <div>
        {finish ? (
          <form onSubmit={this.handleSubmit}>
            <input
              type='text'
              value={this.state.input}
              onChange={this.handleChange}
              style={{
                color: "#eeeeee",
                width: "100px",
                height: "20px",
                backgroundColor: "rgba(238,238,238,0.3)",
                margin: "30px 30px 20px",
                borderRadius: "8px",
              }}
            />
            <button
              style={{
                color: "white",
                backgroundColor: "rgba(238,238,238,0.3)",
                padding: "5px",
                borderRadius: "8px",
              }}
            >
              Next
            </button>
          </form>
        ) : (
          <form onSubmit={this.handleSubmit}>
            <button
              onClick={this.handleClick}
              style={{
                color: "white",
                backgroundColor: "rgba(238,238,238,0.3)",
                padding: "5px",
                borderRadius: "8px",
                margin: "20px",
              }}
            >
              Continue
            </button>
          </form>
        )}
      </div>
    );
  }

  handleChange = (e) => {
    const re = /^[0-9\b]+$/;
    if (e.target.value === "" || re.test(e.target.value)) {
      this.setState({ value: e.target.value });
      this.setState({
        input: e.currentTarget.value,
      });
    }
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const { onSubmit } = this.props;
    const newScore = Number(this.state.input);
    if (newScore > 180) return alert("0から180の間で入力してよ");
    onSubmit(newScore);
    this.setState({
      input: "",
    });
  };

  handleClick = () => {
    const { onClick } = this.props;
    onClick();
  };
}

export default FormCountUp;
